.profile-container{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-width: fit-content;
    background-image: url('../../../src/assests/Home/bg.png');
}
.profile-parent{
    display: flex;
    align-items: center;
    color: aliceblue;
}
.profile-details-name{
    font-size: 24px;
}


.btn-parimary-btn{
    color: white;
    border: 2px solid #ff2cdf;
    border-radius: 50px;
    font-size: 12px;
    background-color: #080808;
}
.btn-parimary-btn:hover{
    color: white;
    border: 2px solid tomato;
    background-color: black;
}



.highligted-btn{
    color: white;
    border: 2px solid #ff2cdf;
    border-radius: 50px;
    font-size: 12px;
    background-color: #080808;
}
.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 10px 0 24px 0;

}
.profile-details-role h1{
    font-size: 40px;
}
.primary-text{
    color: white;
}
.hightted-text{
   color: tomato; 
}
.profile-role-tagline{
   font-size: 19px; 
   margin: 5px 0 0 0;
}
.profile-options .highligted-btn{
    margin: 0 0 0 24px;
}
.profile-options .highligted-btn:hover{
    margin: 0 0 0 24px;
    background-color: white;
    color: tomato;
}

.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 0px #ff2cdf;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;

}
.profile-picture-background{
   height:92%; 
   width: 92%;
   background-image: url('../../../src/assests/Home/palash.jpg');
   background-size: cover;
   border-radius: 50%;
   background-repeat: no-repeat;
   background-position: center;
}
.profile-picture-background:hover{
    transform: scale(1.07);
    transition: 1s ease-out;
}


.colz .colz-icon i{
    color: white;
    padding: 8px;
    padding-left:0px ;
}
.colz .colz-icon i:hover{
    transform: scale(1.5);
}

@media screen and (max-width: 1110px) {
.profile-picture{
    margin: 0 0px 0 80px ;
}

.profile-details-name{
    font-size: 20px;
}
.profile-details-role .primary-text{
    font-size: 12px;
}
.profile-options .btn{
    width: 120px;
    padding: 11px 0;
    z-index: 700;

}
    
}
@media screen and (max-width: 910px){

.profile-picture{
    height: 320px;
    width: 320px;
    margin: 0 0px 0 4px ;
}
}
@media screen and (max-width: 810px){

.profile-picture{
    
    margin: 0 !important;
}
.profile-parent{
    flex-direction: column-reverse;
    margin: 40px 0 0 0;
}

.profile-options{
    display: flex;
    justify-content: space-between;

}
.profile-details{
  margin: 25px 0 0 ;  
}
.profile-details .highligted-btn{
    margin: 0 !important;
}
.profile-options .profile-details-role{
    text-align: center;
}

}
@media screen and (max-width: 375px){
.profile-picture{
    height: 275px;
    width: 275px;
    margin-top: -90px;

}
.profile-details{
    width: 90%;
}



}

